@import "boards.css"; /* Reset CSS */
/* Box sizing rules */
html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* Remove default margin */
body, h1, h2, h3, p, figure, blockquote, dl, dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements */
ul, ol {
  padding: 0;
  list-style: none;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove typeface styles in all browsers (opinionated) */
body, button, input, select, textarea {
  font-family: sans-serif;
}

/* Remove background and border in Firefox/IE */
button, input {
  overflow: visible;
}

/* Address Firefox 4+ setting line-height on input using !important in the UA stylesheet */
button, input {
  line-height: normal;
}

/* Reset blockquote styling to not be italicized */
blockquote {
  font-style: normal;
}

/* Reset button styling */
button {
  background: transparent;
  padding: 0;
}

/* Reset input styling */
input:focus {
  outline: none;
}

/* Reset select styling */
select:focus {
  outline: none;
}

/* Reset textarea styling */
textarea:focus {
  outline: none;
}

/* Reset anchor styling */
a {
  color: inherit;
  text-decoration: none;
}

/* Remove border on images inside links in IE 10 */
img {
  border-style: none;
}

/* Responsive images */
img {
  max-width: 100%;
  height: auto;
}

/* Add a pointer cursor to links with no underlines */
a {
  cursor: pointer;
}

/* Style the hr element */
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

/* Responsive embeds */
embed,
iframe,
object,
audio,
video {
  max-width: 100%;
}

/* Remove default padding on media elements in Firefox 4+ */
video {
  padding: 0;
}

/* Reset form styles */
form {
  display: block;
  margin: 0;
}

/* Style the mark element */
mark {
  background-color: #ff0;
  color: #000;
}

/* Style the abbr element: https://davidwalsh.name/html5-attr */
abbr[title] {
  border-bottom: 1px dotted;
}

/* Reset heading margins */
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

/* Reset paragraph margins */
p {
  margin-top: 0;
  margin-bottom: 1rem;
}

.auth__bg {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
  height: 100vh;
  width: 100vw;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: white;
  width: 500px;
  height: 300px;
  border: 10px lavender solid;
  border-radius: 20px;
}
.auth__content > div {
  margin: 10px 0;
}

.auth__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}
.auth__buttons > div {
  font-size: 26px;
  margin: 0 10px;
  background: lavender;
  padding: 4px 12px;
  border-radius: 15px;
}
.auth__buttons > div:hover {
  cursor: pointer;
  background: dodgerblue;
  color: white;
}
.auth__buttons > div.active {
  color: white;
  background: dodgerblue;
}
.auth__buttons > div.active:hover {
  background: blue;
}

.ui__input input {
  font-size: 26px;
  padding: 2px;
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: black;
}

